import React, { Component } from 'react'
import { connect } from 'react-redux'

import SpeedDial from '@material-ui/lab/SpeedDial';
import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon';
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';

import { FaSave} from 'react-icons/fa'
import { AiOutlineUpload } from 'react-icons/ai'
import { SiReadthedocs } from 'react-icons/si'
import { setActivePage } from '../../redux/actions'

import PythonApi from '../../apis/PythonApi'

import { MdDashboard } from 'react-icons/md';

const useStyles = makeStyles((theme) => ({
    root: {
        transform: 'translateZ(0px)',
        flexGrow: 1,
    },
    exampleWrapper: {
        position: 'relative',
        marginTop: theme.spacing(3),
    },
    radioGroup: {
        margin: theme.spacing(1, 0),
    },
    speedDial: {
        position: 'absolute',
        '&.MuiSpeedDial-directionUp, &.MuiSpeedDial-directionLeft': {
            bottom: theme.spacing(2),
            right: theme.spacing(2),
        },
        '&.MuiSpeedDial-directionDown, &.MuiSpeedDial-directionRight': {
            top: theme.spacing(2),
            left: theme.spacing(2),
        },
    },
}));

function SpeedDials(props) {
    const classes = useStyles();
    const direction = React.useState('left')[0];
    const [open, setOpen] = React.useState(false);
    const hidden = React.useState(false)[0];

    const handleClose = () => {
        setOpen(false);
    };

    const handleOpen = () => {
        setOpen(true);
    };

    return (
        <SpeedDial
            ariaLabel="SpeedDial example"
            className={classes.speedDial}
            hidden={hidden}
            icon={<SpeedDialIcon />}
            onClose={handleClose}
            onOpen={handleOpen}
            open={open}
            direction={direction}
        >
            {props.actions.map((action) => (
                <SpeedDialAction
                    key={action.name}
                    icon={action.icon}
                    tooltipTitle={action.tip || action.name}
                    tooltipPlacement={"top"}
                    onClick={action.onClick}
                    href={action.href}
                />
            ))}
        </SpeedDial>
    );
}

class Controls extends Component {

    hotKeysHandler = {
        SAVE_SESSION: () => this.saveCurrentSession(),
        LOAD_SESSION: () => this.loadSession()
    }

    syncWithSession = (session) => {
        document.dispatchEvent(new CustomEvent("syncWithSession", {detail: {session}}))
    }

    showPage = (pageName) => {
        this.props.setActivePage(pageName)
    }

    saveCurrentSession = () => {
        let path = prompt("Please provide the path to save the session.\n\nRoot directory:\n" + this.props.session.settings.rootDir)
        if (!path) return 

        PythonApi.saveSession(path)
    }

    loadSession = () => {

        let path = prompt("Please provide the path of the saved session.\n\nRoot directory:\n" + this.props.session.settings.rootDir)
        if (!path) return

        PythonApi.loadSession(path)
    }

    render() {

        const actions = [{
            name: "Save session",
            icon: <FaSave size={20} />,
            onClick: this.saveCurrentSession
        }, {
            name: "Load session",
                icon: <AiOutlineUpload size={20} />,
            onClick: this.loadSession
        }, this.props.active.page === "sislDocs" ? {
            name: "GUI",
                icon: <MdDashboard size={20} />,
            onClick: () => this.showPage("plots"),
        } : {
            name: "Sisl docs",
                icon: <SiReadthedocs size={20} />,
            onClick: () => this.showPage("sislDocs"),
        }]

        return <SpeedDials actions={actions} />
    }
}

export class Control extends Component {

    render(){
        
        return <Button
                    data-tip={this.props.tooltip}
                    style={this.props.style}
                    floating
                    icon={this.props.icon}
                    onClick={this.props.onClick}
                    className={this.props.className} />
    }
}

const mapStateToProps = state => ({
    session: state.session,
    active: state.active
})

const mapDispatchToProps = {
    setActivePage
}

export default connect(mapStateToProps, mapDispatchToProps)(Controls);
